<template>
    <div>
        <div class="container add-page new-menus  mt-3">
            <div class="form-container">
                <div class="form-group">
                    <label for="">Type</label>
                    <select class="form-control" name="pageType" id="pageType" v-model="pagetype">
                        <option value="" selected>Select Page Type</option>
                        <option value="page">Page</option>
                        <option value="custom">Custom Link</option>
                    </select>
                </div>
                <!-- FOR PAGE TYPE -->
                <div class="form-group" v-if="pagetype == 'page'">
                    <label for="">Page</label>
                    <select class="form-control" name="pageid" id="pageid" v-model="pageid">
                        <option v-for="(page, i) in pages" :key="i" :value="page.page_id">
                            {{page.title}}
                        </option>
                    </select>
                </div>
                <!-- FOR CUSTOM MENU -->
                <div class="form-group" v-if="pagetype == 'custom'">
                    Label
                    <input class="form-control" type="text" name="customlabel" id="customlabel" v-model="customlabel"/>
                </div>
                <div class="form-group" v-if="pagetype == 'custom'">
                    URL
                    <input class="form-control" type="text" name="customurl" id="customurl" v-model="customurl"/>
                </div>
                
                <div class="form-check mb-2" v-if="pagetype">
                    <input class="form-check-input" type="checkbox" name="target" id="" v-model="target">
                    <label class="form-check-label" for="target">
                        Open the link/page in new window
                    </label>
                </div>

                <div class="form-group">
                    <button class="btn btn-success" v-on:click="add_page">Add Page</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            selected_pages: [
                { 
                    page: 0,
                    type: "",
                    label: "",
                    css: "",
                    url: "",
                    target: "",
                    structure: []
                }
            ],
            pagetype: "",
            pageid: 0,
            customlabel: "",
            customurl: "",
            target: ""
        }
    },
    props: {
        pages: {
            required: true,
            type: Array
        }
    },
    methods: {
        add_page: function(){
            let page_title, url;
            if ( this.pagetype == 'page' ){
                page_title = this.pages.find(x => x.page_id == this.pageid).title;   // GET THE TITLE FROM PAGES JSON
                url = "";
            }
            if ( this.pagetype == 'custom' ){
                page_title = this.customlabel;   // GET THE TITLE CUSTOM'S LABEL
                url = this.customurl;
            }
            this.selected_pages = {
                page: this.pageid,
                type: this.pagetype,
                label: page_title,
                css: "",
                structure: [],
                url: url,
                target: this.target
            };
            this.$emit("selected_pages", this.selected_pages);
        }
    }
}
</script>

<style>
    .new-menus .row{ width: 100%; margin-bottom: 30px; }
</style>